import React from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import './OrderedForm';
export default function OrderedForm() {
  return (
    <Container className="my-5">
      <Row>
        <Col md={8}>
          <div className="form-section">
            <h4> </h4>
            <Form>
              <Form.Group className="mb-3" controlId="name">
                <Form.Label> *</Form.Label>
                <Form.Control type="text" required />
              </Form.Group>

              <Form.Group className="mb-3" controlId="city">
                <Form.Label> /  *</Form.Label>
                <Form.Control type="text" required />
              </Form.Group>

              <Form.Group className="mb-3" controlId="region">
                <Form.Label> *</Form.Label>
                <Form.Control type="text" required />
              </Form.Group>

              <Form.Group className="mb-3" controlId="address">
                <Form.Label>  (  -   -  - ) *</Form.Label>
                <Form.Control type="text" required />
              </Form.Group>

              <Form.Group className="mb-3" controlId="phone">
                <Form.Label>  *</Form.Label>
                <Form.Control type="tel" required />
              </Form.Group>

              <Form.Group className="mb-3" controlId="notes">
                <Form.Label>   ()</Form.Label>
                <Form.Control as="textarea" rows={3} />
              </Form.Group>

              <Form.Group className="mb-3" controlId="discount">
                <Form.Label>  </Form.Label>
                <Form.Control type="text" />
                <Button variant="secondary" className="mt-2">  </Button>
              </Form.Group>

              <Form.Check 
                type="radio" 
                id="cashOnDelivery" 
                label="   " 
                name="paymentMethod" 
                defaultChecked 
                className="my-3" 
              />
            </Form>
          </div>
        </Col>

        <Col md={4}>
          <div className="form-section">
            <h4></h4>

            <div className="bottom-space">
              <p> 12   1037  1</p>
              <p className="total-font-bold">180.00 </p>
            </div>

            <div className="bottom-space">
              <p></p>
              <p className="total-line-through">360.00 </p>
            </div>

            <div className="bottom-space">
              <p>  </p>
              <p className="total-font-bold">180.00 </p>
            </div>

            <div className="bottom-space">
              <p></p>
              <p>40.00 </p>
            </div>

            <div className="bottom-space">
              <p></p>
              <p className="total-font-bold">220.00 </p>
            </div>

            <Button variant="primary" className="w-100"> </Button>
          </div>
        </Col>
      </Row>
    </Container>
  );
}
