import React from 'react';
import './Header.css';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const Header = () => {
  return (
    <header className="header">
      <div className="top-bar">
        <p>   100   +999</p>
      </div>
      <Navbar expand="lg" className="navbar">
        <div className="container-fluid justify-content-center">
          <Navbar.Brand href="/" className="navbar-logo">
            <img src="https://doniccieg.com/storage/2023/02/donicci-social.png" alt="Donicci Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbar-nav" />
          <Navbar.Collapse id="navbar-nav" className="justify-content-center">
            <Nav className="nav-links">
              <NavDropdown title="" id="men-dropdown">
                <NavDropdown.Item as={Link} to="/menShoe">
                   
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/ManSocks">
                   
                </NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="" id="women-dropdown">
                <NavDropdown.Item as={Link} to="/WomenSocks">
                   
                </NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="" id="kids-dropdown">
              <NavDropdown.Item as={Link} to="/ChildSocks">
                   
                </NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="" id="offers-dropdown">
                <NavDropdown.Item href="#offer1"> 1</NavDropdown.Item>
                <NavDropdown.Item href="#offer2"> 2</NavDropdown.Item>
              </NavDropdown>
            </Nav>
            <div className="nav-icons d-flex align-items-center">
              <i className="fas fa-shopping-cart"></i>
              <i className="fas fa-heart"></i>
              <i className="fas fa-user"></i>
            </div>
          </Navbar.Collapse>
        </div>
      </Navbar>
    </header>
  );
};

export default Header;
