import React from 'react'
import { useState } from 'react';
import './ManSocks.css'
const ManSocks= () => {
    const productsData = [
        { name: " 3 ", price: 45, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 120, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 90, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 80, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 75, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 96, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 45, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 40, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 130, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 70, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 75, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 50, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 30, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 120, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 90, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 45, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 75, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 100, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 45, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 110, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 90, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 78, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 99.5, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 140, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 55, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 60, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 44, discount: 34, img: "img1.jpg" },
        { name: " 3 ", price: 80, discount: 52, img: "img1.jpg" },
        { name: " 5 ", price: 75, discount: 50, img: "img1.jpg" },
        { name: " 6 ", price: 112, discount: 34, img: "img1.jpg" },
    ];

    const [products, setProducts] = useState(productsData);

    const sortProducts = (sortValue) => {
        let sortedProducts;
        if (sortValue === "priceLowToHigh") {
            sortedProducts = [...products].sort((a, b) => a.price - b.price);
        } else if (sortValue === "priceHighToLow") {
            sortedProducts = [...products].sort((a, b) => b.price - a.price);
        } else {
            sortedProducts = productsData; 
        }
        setProducts(sortedProducts);
    };

    return (
        <div className="container">
            <div className="row mb-4">
                <div className="col-md-6">
                    <label htmlFor="sort" className="form-label"> </label>
                    <select
                        id="sort"
                        className="form-select"
                        onChange={(e) => sortProducts(e.target.value)}
                    >
                        <option value="default"> </option>
                        <option value="priceLowToHigh"> :  </option>
                        <option value="priceHighToLow"> :  </option>
                    </select>
                </div>
            </div>

            <div className="row" id="product-list">
                {products.map((product, index) => {
                    const discountedPrice = product.price - (product.price * (product.discount / 100));
                    return (
                        <div className="col-md-4" key={index}>
                            <div className="product-card">
                                <img src={product.img} alt={product.name} />
                                <h3>{product.name}</h3>
                                <p className="original-price"> {product.price.toFixed(2)}</p>
                                <p className="discounted-price"> {discountedPrice.toFixed(2)}</p>
                                <button className="btn btn-primary">  </button>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default ManSocks;